const apiKey = "sk-9741643783ec477685dfb26a9ce04727";

export const askDS = async (messages, onDataChunk) => {
  try {
    const response = await fetch(
      "https://api.deepseek.com/v1/chat/completions",
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${apiKey}`, // key
          "Content-Type": "application/json", // json
        },
        body: JSON.stringify({
          model: "deepseek-chat",
          messages,
          max_tokens: 4096,
          temperature: 1.0,
          stream: true, // false
        }),
      }
    );

    const reader = response.body.getReader();
    const decoder = new TextDecoder("utf-8");
    let buffer = ""; // 

    while (true) {
      const { done, value } = await reader.read();
      if (done) break; // 

      const chunk = decoder.decode(value, { stream: true });
      buffer += chunk; // 

      // 
      const lines = buffer.split("\n");

      // 
      buffer = lines.pop();
      for (const line of lines) {
        if (line.startsWith("data: ")) {
          const jsonStr = line.slice(6).trim(); //  "data: " 
          if (jsonStr !== "[DONE]") {
            try {
              const jsonData = JSON.parse(jsonStr);
              const content = jsonData?.choices[0]?.delta?.content || "";
              const isStop = jsonData?.choices[0]?.finish_reason || "";
              //   
              onDataChunk(content, isStop === "stop");
            } catch (error) {
              console.error(" JSON :", error, line);
            }
          }
        }
      }
    }
  } catch (error) {
    console.error("", error);
    return null;
  }
};
